
.swiper {
    width: 100%;
    height: 100%;
  }
  
  .swiper-slide {
    text-align: center;
    font-size: 18px;
    // background: #fff;
  
    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }
  
  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .swiper-horizontal>.swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal, .swiper-pagination-custom, .swiper-pagination-fraction {
    bottom: 10px !important;
  }

  .swiper-pagination-bullet {
    width: 15px !important;
    height: 15px !important;
    border: 1px solid rgb(80,213,183) !important;
    background: rgb(80,213,183) !important;
background: linear-gradient(45deg, rgba(80,213,183,1) 0%, rgba(6,125,104,1) 100%) !important;
border-radius: 5px !important;
  }

  .swiper-wrapper {
    margin-bottom: 35px;
  }
