.main-404 {
  width: 100vw;
  height: 100vh;
  background-image: url("../../asset/image/bg404.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;

  h1 {
    font-size: 15em;
    color: $white;
    margin: 0;
  }

  h5 {
    font-size: 2em;
    color: $white;
    margin: 0;
  }

  button {
    margin-top: 50px;
    padding: 10px 30px 10px 30px;
    font-family: "carbonFontBold";
    border-radius: 10px;
    cursor: pointer;
    // background-color: transparent;
    // border: 1.5px solid $white;
    // color: $white;
  }
}
