.progress-line {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;

  .title-progress-lineBar {
    margin-left: 20px;
  }

  .progress-container {
    display: flex;
    align-items: center;
  }

  .progress-lineBar {
    background-color: #f7f7f7;
    border-radius: 20px;
    position: relative;
    // margin: 15px 0;
    height: 30px;
    width: 300px;
  }

  .progress-done-lineBar {
    // background: linear-gradient(to left, #F2709C, #FF9472);
    // box-shadow: 0 3px 3px -5px #F2709C, 0 2px 5px #F2709C;
    border-radius: 20px;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 0;
    opacity: 0;
    transition: 1s ease 0.3s;
  }

  .progress-lineBar-green {
    background: rgb(80, 213, 183);
    background: linear-gradient(
      45deg,
      rgba(80, 213, 183, 1) 0%,
      rgba(6, 125, 104, 1) 100%
    );
    box-shadow: 0 3px 3px -5px rgba(80, 213, 183, 1),
      0 2px 5px rgba(6, 125, 104, 1);
  }
  .progress-lineBar-red {
	background: rgb(254,174,150);
	background: linear-gradient(45deg, rgba(254,174,150,1) 0%, rgba(254,9,68,1) 100%);
    box-shadow: 0 3px 3px -5px rgba(254,174,150,1),
      0 2px 5px rgba(254,9,68,1);
  }
  .progress-lineBar-yellow {
	background: rgb(251,176,52);
background: linear-gradient(45deg, rgba(251,176,52,1) 0%, rgba(255,221,0,1) 100%);
box-shadow: 0 3px 3px -5px rgba(251,176,52,1),
      0 2px 5px rgba(255,221,0,1);
  }
}
