.main-home {
  width: 100%;
}

.header-home {
  background-image: url("../../asset/image/HeaderIndexRaw.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-bottom: 100px;
  width: 100%;
  position: relative;
}

.gradient-header-image {
  height: 100px;
  width: 100%;
  position: absolute;
  bottom: 0;
  background: rgb(255, 255, 255);
  background: linear-gradient(
    -180deg,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 255, 255, 1) 93%,
    rgba(255, 255, 255, 1) 100%
  );
}

.description-home {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  h1 {
    text-align: center;
    font-size: 2em;
    margin: 0;
    margin-top: 40px;
  }
  h5 {
    text-align: center;
    font-family: "carbonFont";
    font-size: 1.4em;
  }
}

.green-bold-text {
  font-family: "carbonFontBold";
  color: $green-high;
}

.text-description-home {
  width: 100%;
  max-width: 1366px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: column;
  margin-top: 50px;
  font-size: 1.2em;
}

.standard-home {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  margin-top: 50px;
  margin-bottom: 50px;
}

.img-standard {
  width: 300px;
  opacity: 0.2;
}

.box-text-standard {
  position: absolute;
  h1 {
    font-size: 3em;
  }
  p {
    text-align: center;
    font-size: 1.1em;
  }
}

.calculator-home {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.container-calculator-home {
  width: 100%;
  max-width: 1366px;
  display: flex;
  border-radius: 25px;
  flex-direction: column;
  align-items: center;
  background: rgb(6, 125, 104);
  background: linear-gradient(
    162deg,
    rgba(6, 125, 104, 1) 0%,
    rgba(80, 213, 183, 1) 100%
  );
  padding-bottom: 50px;
    padding-top: 25px;
}

.box-calculator-home {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 50px;
  padding-right: 50px;
}

.box-input-p-calculator-home {
  display: flex;
  align-items: center;
  margin-right: 50px;

  .box-result-calculator {
    display: flex;
    color: $white;
    font-family: "carbonFontBold";
    margin-right: 10px;
  }
}

.inptu-calculator-home {
  height: 32px;
  border-radius: 10px;
  margin-right: 20px;

}

.btn-calculator-home {
  height: 32px;
  border-radius: 10px;
  // margin-right: 20px;
  margin-left: 50px;
  border: none;
  background-color: $green-high;
  color: $white;
  padding: 0px 30px 0px 30px;
  font-weight: bold;
  border: 1px solid $white;
  cursor: pointer;
}

.customers-home {
  margin-top: 30px;
  margin-bottom: 30px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  h1 {
    text-align: center;
  }
}

.customers-container {
  width: 100%;
  max-width: 1366px;
  display: flex;
}

.description-customers {
  display: flex;
  flex-direction: column;
  cursor: pointer;
}

.text-customers {
text-align: start;
}

.title-customers {
  text-align: end;
}


.certificates-home {
  margin-top: 30px;
  margin-bottom: 30px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  h1 {
    text-align: center;
  }

  margin-bottom: 100px;
}

.certificates-container {
  width: 100%;
  max-width: 1366px;
  display: flex;
}

.description-certificates {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

footer {
  background-color: $green-high;
  color: black;
  height: 55px;
  display: flex;
  justify-content: center;
  left: 0;
  bottom: 0;
  width: 100%;
  // position: absolute;
  z-index: 100;
  // margin-top: 55px;

}



.suggestion-home {
  margin-top: 30px;
  margin-bottom: 30px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  h1 {
    text-align: center;
  }
}

.suggestion-container {
  width: 100%;
  max-width: 1366px;
  display: flex;
}

.box-suggestion {
  // background-color: transparent;
  border: 1px solid rgb(80,213,183);
  border-radius: 20px;
  width: 50%;
  padding: 50px;
  margin-bottom: 25px;
  cursor: pointer;
}

.text-suggestion {
  color: white;
  // background: transparent !important;
}