.calculator-payment {
  display: flex;
  flex-direction: column;
  align-items: center;
  // margin-bottom: 200px;
  background-color: $green-light;
}

.calculator-payment-container {
  display: flex;
  justify-content: center;
  max-width: 1366px;
  width: 100%;
  margin-bottom: 230px;
  flex-direction: column;
  align-items: center;
  h1 {
    color: $white;
    font-size: 2.5em;
    text-align: center;
  }
}


.choise-calculator-offset {
  width: 100%;
  display: flex;
    justify-content: space-around;
  .container-label {
    border: 1px solid $white;
    padding: 10px;
    border-radius: 10px;
    width: 210px;
    p {
      display: inline-block;
      margin-right: 5px;
      margin-top: 0;
      margin-bottom: 0;
      font-family: "carbonFontBold";
      font-size: .8em;
    }
    span {
      margin-top: 13px;
    margin-left: 10px;
    }
  }
}

.box-input-choise-calculator-offset {

}

.price-calculator-offset-item {
  border: 1px solid $white;
    border-radius: 0px 0px 10px 10px;
    margin-top: -22px;
    border-top: none;
    height: 55px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 10px;
    padding-right: 10px;
    font-family: "carbonFontBold";
    p {
      margin: 0;
      padding-top: 10px;
    }
}

.price-all-calculator-offset {
  display: flex;
  align-items: center;
  margin-top: 142px;
  font-family: "carbonFontBold";
  color: $white;
  p{
    margin-right: 10px;
  }
}

.payment-offset-btn {
  background-color: $green-light;
  width: 300px;
  padding: 10px;
  border: 1px solid $white;
  color: $white;
  border-radius: 10px;
  font-family: "carbonFontBold";
  font-size: 1.2em;
  cursor: pointer;
}