.progress {
  position: relative;
  width: 240px;
  margin-top: 50px;

  svg {
    width: 240px;
    height: 240px;
  }

  svg #circle {
    stroke-width: 6.5;
    transition: all 4s ease-in-out;
    z-index: 2;
  }
}

#circleRemaining,
#progress {
  position: absolute;
}

#circle,
#circleRemaining {
  fill: none;
  stroke: white;
  stroke-linecap: round;
}

#circleRemaining {
  stroke-width: 3.5;
  opacity: 0.3;
  left: 3px;
  top: 3px;
  z-index: 1;
}

/* --------------------------
    Keyframes
  --------------------------- */

@keyframes happy-look {
  0% {
    transform: translate(0px, -45px);
  }
  30% {
    transform: translate(-10px, -20px);
  }
  60% {
    transform: translate(-25px, 20px);
  }
  70% {
    transform: translate(-20px, 0px);
  }
  80% {
    transform: translate(10px, 0px);
  }
  90% {
    transform: translate(0px, -0px);
  }
  100% {
    transform: translate(0px, 0px);
  }
}
@keyframes happy-mouth {
  0% {
    height: 50px;
  }
  10% {
    height: 50px;
  }
  20% {
    height: 50px;
  }
  30% {
    height: 50px;
  }
  60% {
    height: 50px;
  }
  70% {
    height: 35px;
  }
  80% {
    height: 35px;
  }
  90% {
    height: 30px;
  }
}

@keyframes happy-eye {
  0% {
    height: 10px;
  }
  10% {
    height: 12px;
  }
  20% {
    height: 12px;
  }
  30% {
    height: 12px;
  }
  59% {
    height: 15px;
  }
  60% {
    height: 1px;
  }
  61% {
    height: 15px;
  }
  66% {
    height: 1px;
  }
  67% {
    height: 15px;
  }
  68% {
    height: 15px;
  }
  70% {
    height: 15px;
  }
  80% {
    height: 15px;
  }
  100% {
    height: 15px;
  }
}

/* --------------------------
    SVG Styles
  --------------------------- */

#progress * {
  -webkit-transform-origin: 50% 51.5%;
  transform-origin: 50% 51.5%;
  transform-box: fill-box;
}

.emoji {
  display: flex;
  justify-content: center;
  margin: auto;
  width: 90px;
  height: 90px;
  background-color: transparent;
  border-radius: 50%;
  position: relative;
  top: 70px;
  left: 0px;
}

.emoji.happy .face {
  animation: happy-look 5s 0.7s forwards;
}

.face .eye {
  width: 15px;
  height: 15px;
  background-color: white;
  border-radius: 50%;
  top: 25px;
  position: absolute;
  animation: happy-eye 5s 0.5s forwards;
}

.face .eye.left {
  left: 20px;
}

.face .eye.right {
  right: 20px;
}

.face .mouth {
  width: 55px;
  height: 50px;
  border-style: solid;
  border-radius: 50%;
  border-width: 6px;
  border-color: transparent transparent white transparent;
  left: -33px;
  top: 15px;
  position: absolute;
  animation: happy-mouth 5s forwards;
}

/* --------------------------
    Dribbble
  --------------------------- */

.dribbble {
  position: fixed;
  display: block;
  right: 30px;
  bottom: 30px;
}

.dribbble svg {
  width: 30px;
  height: 30px;
}
