.main-calculator {
  width: 100%;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  justify-content: space-between;
}

.calculator {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 200px;
  // background-color: $green-light;
}

.header-container-pages {
  background-image: url("../../asset/image/headerImg.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  position: relative;
}

.calculator-title-top {
  text-align: center;
}

.calculator-container {
  display: flex;
  justify-content: space-between;
  max-width: 1366px;
  width: 100%;
}

.menu-calculator {
}

.container-content-calculator {
  display: flex;
  width: 100%;
  justify-content: center;
  flex-direction: column;
}

.item-menu-calculator {
  font-family: "carbonFontBold";
  width: 200px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 1.1em;
  padding: 5px;
  margin: 10px;
  background-color: transparent;
  border: none;
  color: $green-very-high;
  cursor: pointer;
  i {
    margin-left: 10px;
  }
}

.box-title-content-calculator {
  text-align: center;
}

.contetn-calculator {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.row-form-input-calculator {
  align-items: flex-start;
  display: flex;
  margin-right: 15px;
  margin-left: 17px;
  label {
    margin-left: 5px;
    margin-top: 5px;
  }
  input {
    border: 2px solid $green-high;
    border-radius: 5px;
    height: 28px;
    padding: 2px 5px;
  }
  button {
    width: 185px;
    height: 36px !important;
    background-color: transparent;
    border: 2px solid $green-high;
    border-radius: 5px;
    height: 28px;
    padding: 2px 5px;
  }
}

.box-row-input-calculator {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  margin-bottom: 20px;
}

.btn-calculator-next {
  align-self: flex-end;
  background-color: transparent;
  border: none;
  color: $green-very-high;
  font-family: "carbonFontBold";
  font-size: 1em;
  cursor: pointer;
  // margin-bottom: 100px;
}

.btn-calculator-calculation {
  background-color: transparent;
  border: 2px solid $green-high;
  width: 90%;
  height: 50px;
  border-radius: 10px;
  align-self: center;
  cursor: pointer;
}

.align-item-calculator {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.box-result-calculator {
  align-self: center;
  // width: 90%;
  display: flex;
  align-items: center;
  position: relative;
  p {
    margin-right: 10px;
    font-family: "carbonFontBold";
  }
}

.offset-btn {
  background-color: $green-high;
  border: none;
  color: $white;
  height: 70px;
  border-radius: 15px;
  font-family: "carbonFontBold";
  font-size: 1.2em;
  width: 50%;
  align-self: center;
  margin-top: 100px;
}


.result-calculator-child-text {
  background: rgb(80, 213, 183);
    background: linear-gradient(45deg, rgb(80, 213, 183) 0%, rgb(6, 125, 104) 100%);
    border-radius: 7px;
    color: #ffffff !important;
    padding: 15px 10px 15px 10px;
}

.box-input-search {
  position: relative;
}

.ul-select-search {
  position: absolute;
  max-height: 200px;
  width: 185px;
  overflow-y: auto;
  padding-inline-start: 0px;
}