@import '../variables/fonts.scss';
@import '../variables/variable.scss';
@import '../variables/colors.scss';

body {
    font-family: $font-family-main ;
	width: 100%;
	
}

::-webkit-scrollbar {
    width: 10px;
}
 
::-webkit-scrollbar-track {
    background-color: #ebebeb;
    -webkit-border-radius: 10px;
    border-radius: 10px;
}

::-webkit-scrollbar-thumb {
    -webkit-border-radius: 10px;
    border-radius: 10px;
    background: $green-high; 
}


html body {
	margin: 0;
	padding: 0;
	direction: rtl;
	display: flex;
	font-family: 'carbonFont';
}

main {
	width: 100%;
	// max-width: 1366px;
	display: flex;
    justify-content: center;
}

#root {
	width: 100%;
	display: flex;
    justify-content: center;
}

a {
	text-decoration: none;
	color: $green-very-high;
}

ul li {
	list-style: none;
}

button, input {
	font-family: 'carbonFont';
}

input{
    outline: none;
}

select {
	color: $green-very-high;
	font-family: 'carbonFont';

}


label , input {
	color: $green-very-high;
	border:none;
}

h6,h5,h4,h3,h2,h1 {
	color: $green-very-high;
	font-family: 'carbonFontBold';
}

.active-btn {
	background: rgb(80,213,183);
    background: linear-gradient(45deg, rgba(80,213,183,1) 0%, rgba(6,125,104,1) 100%);
    border-radius: 7px;
    color: $white !important;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}


.font-english {
	font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
	font-weight: bold;
}





/* The container */
.container-label {
	display: block;
	position: relative;
	padding-left: 35px;
	margin-bottom: 12px;
	cursor: pointer;
	font-size: 22px;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
  }
  
  /* Hide the browser's default checkbox */
  .container-label input {
	position: absolute;
	opacity: 0;
	cursor: pointer;
	height: 0;
	width: 0;
  }
  
  /* Create a custom checkbox */
  .checkmark {
	position: absolute;
	top: 0;
	left: 0;
	height: 25px;
	width: 25px;
	background-color: transparent;
	border: 1px solid $white;
	border-radius: 50%;
  }
  
  /* On mouse-over, add a grey background color */
  .container-label:hover input ~ .checkmark {
	// background-color: #ccc;
  }
  
  /* When the checkbox is checked, add a blue background */
  .container-label input:checked ~ .checkmark {
	background-color: $green-light;
	border: 1px solid $white;
  }
  
  /* Create the checkmark/indicator (hidden when not checked) */
  .checkmark:after {
	content: "";
	position: absolute;
	display: none;
  }
  
  /* Show the checkmark when checked */
  .container-label input:checked ~ .checkmark:after {
	display: block;
  }
  
  /* Style the checkmark/indicator */
  .container-label .checkmark:after {
	left: 9px;
	top: 5px;
	width: 5px;
	height: 10px;
	border: solid white;
	border-width: 0 3px 3px 0;
	-webkit-transform: rotate(45deg);
	-ms-transform: rotate(45deg);
	transform: rotate(45deg);
  }