.dropdown {
  position: relative;
  // width: 230px;
  margin-left: 40px;
  // margin-bottom: 40px;
  // margin-top: 40px;
  filter: url(#goo);
  z-index: 2;
  cursor: pointer;

  .dropdown-custom {
    padding: 10px !important;
    width: 100px;
  }

  &__face,
  &__items {
    background-color: #fff;
    padding: 20px;
    border-radius: 25px;
  }

  &__face {
    display: block;
    position: relative;
    cursor: pointer;
    text-align: center;
    margin-bottom: 40px;
  }

  &__face:hover {
    display: block;
    position: relative;
    cursor: pointer;
    .dropdown__arrow {
      display: inline;
    }
  }

