.dropdown__items {
  display: none;
  position: absolute;
  background-color: $hover-menu;
  margin-top: 0px;
  right: 0;
  top: 50%;
  width: 100%;
  list-style: none;
  list-style-type: none;
  z-index: -1;
  // opacity: 0;
  animation-name: logo-runLoading;
  animation-duration: 0.6s; /* or: Xms */
  animation-direction: alternate; /* or: normal */
  animation-timing-function: ease-in-out; /* or: ease, ease-in, ease-in-out, linear, cubic-bezier(x1, y1, x2, y2) */
  animation-fill-mode: forwards;
  // transition: all 1s cubic-bezier(0.93, 0.88, 0.1, 0.8);
}


@keyframes logo-runLoading {
  0% {
    opacity: 0;
    margin-top: 0px;
  }

  100% {
    opacity: 1;
    margin-top: 35px;
  }
}


.dropdown__items::before {
  content: "";
  background-color: $hover-menu;
  position: absolute;
  bottom: 100%;
  right: 20%;
  height: 30px;
  width: 20px;
}

